#header_wrapper {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  padding: 30px 40px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: transparent;
}

#header_wrapper_scroll {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  padding: 30px 40px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(8, 8, 8, 0.747);
}

#header_logo {
  width: 15%;
  height: 100%;
  flex-basis: 25%;
  padding-bottom: 15px;
}

#header_logo img {
  width: 50%;
  height: inherit;
}

#header_btn {
  flex-basis: 75%;
}

#header_btn button {
  border: none;
  background-color: transparent;
  padding: 15px;
  color: aliceblue;
}

#header_btn button:hover {
  background-color: lightcoral;
}
